import React from "react";

import "./section-1.css"
import Tree from "../../../../../assets/logo.png"

class Section1 extends React.Component {
  render() {
    return (
      <section className="mx-lg-5 home-section1">
        <div className="mx-4">
          <h1 className="h1">How it works</h1>
          <p className="mt-5">
            <span>Step 1 .</span> Pick an amazing price and enter the
            competitor.
          </p>
          <p className="mt-5 mt-lg-0">
            <span>Step 2 .</span> Select your tickets or let the Lucky Dip
            selector choose a letter and number for you.
          </p>
          <p className="mt-5 mt-lg-0">
            <span>Step 3 .</span> Finalize payment. Wait for the Live Draw and
            be lucky...Join our winners podium ! daily winners !
          </p>
          <p className="mt-5 last mt-lg-0">
            Paradise Competitors has already made &nbsp;
            <span> 126 </span> &nbsp;winners and give away &nbsp;
            <span> 26 562,99 </span>&nbsp; of price !
          </p>
          <div className="row sub-section p-lg-3 w-50 mx-auto">
            <div className="col-lg-3 p-0 col-7 col-sm-3 mx-auto m-lg-0 text-center">
              <figure>
                <img className="img-fluid" src={Tree} alt="" />
              </figure>
            </div>
            <div className="col-lg-9 col-12 col-sm-9">
              <h4>You've got be in it to win it !</h4>
              <div className="row">
                <div className="col-lg-7 col-sm-8 col-12 p-0 mt-3 ml-lg-3 stars">
                  <i className="far fa-star"></i>
                  <i className="far fa-star"></i>
                  <i className="far fa-star"></i>
                  <i className="far fa-star"></i>
                </div>
                <div className="col-lg-4 col-sm-3 col-12 pl-5 pl-sm-0 pl-lg-0 pr-5 p-lg-0 mt-3 trustpilot">
                  <i className="fas fa-star ml-1 ml-lg-0"></i>
                  <h5 className="ml-2 ml-lg-2">Trustpilot</h5>
                </div>
              </div>
              <div className="row p-4">
                <div className="p-0 col-12 btn-group">
                  <button className="like">
                    <i className="fas fa-thumbs-up"></i>Like 11K
                  </button>
                  <button className="comunity">
                    <i className="fas fa-gift"></i>Join our community
                  </button>
                </div>
              </div>
            </div>
          </div>
          <div className="w-25 text-center mx-auto newsletter">
            <button>Recieve free newsletter</button>
          </div>
        </div>
      </section>
    );
  }
}

export default Section1;
