import React from "react";
import "./navbar.css"
import Logo from "../../../../assets/logo.png"
import England from "../../../../assets/england.png"

class Navbar extends React.Component {
  render() {
    return (
      <header className="navbar navbar-expand-lg navbar-light header">
        <figure classNameName="m-0 p-0 d-flex ml-5">
          <img className="img-fluid mt-0" src={Logo} alt="logo" />
          <figcaption>Paradise Competitions</figcaption>
        </figure>
        <button
          className="navbar-toggler"
          type="button"
          data-toggle="collapse"
          data-target="#navbarSupportedContent"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>

        <nav className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav pl-sm-5 pl-4 pb-5 pb-lg-0 ml-auto mr-5 pt-1">
            <li className="nav-item active">
              <a className="nav-link" href>
                Competitions <span className="sr-only">(current)</span>
              </a>
            </li>
            <li className="nav-item">
              <a className="nav-link" href>
                How to play
              </a>
            </li>
            <li className="nav-item">
              <a className="nav-link" href>
                Winners
              </a>
            </li>
            <li className="nav-item">
              <a className="nav-link" href>
                Gift Shop
              </a>
            </li>
            <div style={{position: "relative"}}>
              <button className="england">
                <img className="img-fluid" src={England} alt="" /> EN
              </button>
              <button className="loginBtn" type="button">
                Client Login
              </button>
              <button className="regBtn" type="button">
                Register
              </button>
            </div>
          </ul>
        </nav>
      </header>
    );
  }
}

export default Navbar;
