import React from "react"

import Paypal from "../../../../assets/paypal.png"
import Bancontact from "../../../../assets/bancontact.svg"
import Mastercard from "../../../../assets/mastercard.png"
import Visa from "../../../../assets/visa.svg"

import Video from "../../../../assets/bg-video.mp4"
import Pic1 from "../../../../assets/1660-gaming-1-4012-249095-290519041917.jpg"
import Pic2 from "../../../../assets/77217552.webp"

import "./footer.css"


class Footer extends React.Component
{
    render(){
        return(
            <footer className="footer">
            <h1>Gift Shop</h1>
            <div className="giftshop">
              <div>
                <video controls>
                    <source src={Video} type="video/mp4" />
                </video>
              </div>
              <div> 
                <figure>
                  <img src={Pic1} alt="" />
                </figure>
              </div>
              <div>
                <figure>
                  <img src={Pic2} alt="" />
                </figure>
              </div>
            </div>
            <div className="btnn">
              <button>FAQ</button>
            </div>
            <div className="payment-method">
              <h3>Payment Method</h3>
              <div>
                <figure>
                  <img src={Bancontact} alt="" />
                </figure>
                <figure>
                  <img src={Visa} alt="" />
                </figure>
                <figure>
                  <img src={Mastercard} alt="" />
                </figure>
                <figure>
                  <img src={Paypal} alt="" />
                </figure>
              </div>
            </div>
            <div className="copyright">
              <p>Copyright 2020 Paradise Composition</p>
              <p>All right reserved</p>
              <p><a href="mailto:">info@paradisecompetitions.eu</a></p>
              <p>Terms and condition</p>
              <p>Privacy policy</p>
              <p>Addilated agreement</p>
            </div>
          </footer>
      
        )
    }
}

export default Footer